/**
 * @file global css样式文件
 * @author chenwen(chenwen@baijiahulian.com)
 */
html,
body,
h1,
h2,
h3,
ul,
p {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
}
body {
  height: 100%;
  font-family: PingFangSC-Regular, "Hiragino Sans GB", "Helvetica Neue", "微软雅黑", "Microsoft Yahei", Helvetica, Arial, sans-serif;
  color: #333;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  background: #F7F7F7;
}
body input, body select, body textarea, body button, body pre {
    font-family: PingFangSC-Regular, "Hiragino Sans GB", "Helvetica Neue", "微软雅黑", "Microsoft Yahei", Helvetica, Arial, sans-serif;
}
body input::-moz-placeholder,
  body textarea::-moz-placeholder {
    color: #999 !important;
    opacity: 1;
}
body input:-ms-input-placeholder,
  body textarea:-ms-input-placeholder {
    color: #999 !important;
}
body input::-webkit-input-placeholder,
  body textarea::-webkit-input-placeholder {
    color: #999 !important;
}
h2 {
  font-size: 14px;
  line-height: 1;
}
h3 {
  font-size: 13px;
  line-height: 1;
}
h1 {
  font-size: 14px;
  line-height: 30px;
  font-weight: normal;
}
ul li {
  list-style: none;
}
a {
  cursor: pointer;
  color: #1a91f2;
  text-decoration: none;
  font-weight: normal;
}
.pointer {
  cursor: pointer;
}
textarea {
  overflow: auto;
}
.vertical-middle,
.middle {
  vertical-align: middle;
  display: inline-block;
}
.none-list {
  text-align: center;
  clear: both;
  padding: 3rem;
  color: #999;
  min-width: 98px;
  min-height: 98px;
}
.none-list:before {
    display: block;
    content: '';
    width: 98px;
    height: 98px;
    background: url("http://p2q80yyoe.bkt.clouddn.com/none-data-bg.png") no-repeat;
    background-size: contain;
    margin: 30px auto;
}
.btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 7px 15px;
  text-align: center;
  font-size: 14px;
  color: #333;
  line-height: 1;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  outline: none;
  cursor: pointer;
}
.btn:hover {
    opacity: .8;
}
.btn-disable {
    background-color: #eef1f6;
    border-color: #d1dbe5;
    color: #bfcbd9;
    cursor: not-allowed;
}
.btn-disable:hover {
    opacity: 1;
}
.btn-blue-light {
    background-color: #fff;
    color: #1a91f2;
    border-color: #1a91f2;
}
.btn-blue-light:hover {
      border-color: #4db3ff;
      color: #4db3ff;
}
.btn-primary {
    background-color: #1a91f2;
    border-color: #1a91f2;
    color: #fff;
}
.btn + .btn {
    margin-left: 6px;
}
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clickable-text {
  color: #666;
  cursor: pointer;
}
.clickable-text:hover {
    color: #4db3ff;
}
.clickable-text-blue {
    color: #1a91f2;
}
.text-success {
  color: #1abc9c;
}
.text-warning {
  color: #f7ba2a;
}
.text-danger {
  color: #ff4949;
}
.text-gray-light {
  color: #999;
}
.text-light-color {
  color: #999;
}
.text-red {
  color: #ff4949;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
pre {
  margin: 0;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.confirm-message-box {
  padding-bottom: 0;
}
